/* Main color #ff003b 255,0,59 */
header {
    background-image: url("https://i.redd.it/uwwte8wps4h91.gif");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.header-content {
    width: 100%;
    height: 855px;
    background: rgb(255, 0, 59, 0.5);
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.header-content img {
    width: 272px;
    height: 272px;
    border-radius: 50%;
    box-shadow: 0 0 50px #FFFFFF;
    transition: 5s ease;
}

.header-content img:hover {
    animation: animation 3.5s infinite;
}

.header-content h1 {
    font-size: 42px;
    text-shadow: 0 0 20px black;
}

.header-content p {
    font-size: 25px;
}

@keyframes animation {
    0% {
        box-shadow: 0 0 50px #FFFFFF;
    }
    10% {
        box-shadow: 0 0 50px #ff003b;
    }
    20% {
        box-shadow: 0 0 50px #ff8c00;
    }
    30% {
        box-shadow: 0 0 50px #ff0;
    }
    40% {
        box-shadow: 0 0 50px #008000;
    }
    50% {
        box-shadow: 0 0 50px #00f;
    }
    60% {
        box-shadow: 0 0 50px #4b0082;
    }
    70% {
        box-shadow: 0 0 50px #9400d3;
    }
    80% {
        box-shadow: 0 0 50px #ff1493;
    }
    90% {
        box-shadow: 0 0 50px #ff69b4;
    }
    100% {
        box-shadow: 0 0 50px #FFFFFF;
    }
}